import React from 'react';
import './footer.css'

import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

function footer(){
    return(
        <div className='footer-div'>
            <div style={{paddingTop:30}}>
              <a href="https://www.instagram.com" className='items'><InstagramIcon/></a>
              <a href="https://www.facebook.com" className='items'><FacebookIcon/></a>
              <a href="https://www.linkedin.com" className='items'><LinkedInIcon/></a>
            <div>
              <h5>CFI shop| copyright 2021</h5>
            </div>
            </div>
        
        </div>
    );
}

export default footer;